.main-navigation {
  width: 100%;
  height: 4.5rem;
  position: fixed;
  top: 13vh;
  z-index: 2;
  background-color: rgb(224, 222, 222);
}

.main-navigation nav {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.main-navigation ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.main-navigation li {
  margin: 0 1rem;
  font-size: 1.5rem;
  
}

.main-navigation a {
  display: block;
  padding: 0.5rem 1rem;
  text-decoration: none;
  color: white;
  border-radius: 5px;
  color: black;
  font-weight: 800;
}

.main-navigation a:hover,
.main-navigation a:active {
  background: white;
  color: #00179b;
}