.section {
    width: 30vw;
    height: 30vh;
    margin-top: 20px;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    text-align: center;
}

.section > div {
    border: 1px solid black;
}
.section > div:hover {
    cursor: pointer;
}

.disabled {
    background-color: grey;
}
.booked {
    background-color: #19a0d9;
}

.section .disabled:hover {
    cursor: not-allowed;
}

button {
    margin-top: 10px;
}

.input_field{
    margin-top: 1rem;
}